<template>
  <div class="shoppingCar">
    <van-nav-bar title="购物车"/>

    <div class="shoppingCard">
      <van-checkbox-group v-model="result">
        <van-cell-group>
          <van-cell
            v-for="(item,index) in list"
            clickable
            :key="item"
            @click="toggle(index)">
            <van-checkbox :name="item" ref="checkboxes" />
            <van-card
              num="2"
              price="2.00"
              desc="描述信息"
              title="商品标题"
              :thumb="imageURL">
            </van-card>
          </van-cell>
        </van-cell-group>
      </van-checkbox-group>
    </div>

    <div class="shoppingSubmit">
      <van-submit-bar
        :price="3050"
        button-text="提交订单"
        @submit="onSubmit">
        <van-checkbox v-model="checked">全选</van-checkbox>
      </van-submit-bar>
    </div>
  </div>
</template>

<script>
  export default {
    data(){
      return{
        imageURL:'https://img.yzcdn.cn/2.jpg',
        result:[],
        list:['a','b','c'],
        checked:''
      }
    },
    methods: {
      toggle(index) {
        this.$refs.checkboxes[index].toggle();
      },
      onSubmit(){
        this.$router.push({path:'./toBePaid'})
      }
    }
  }
</script>

<style scoped lang="scss">
  .shoppingSubmit{
    padding: 0  0.5rem;
  }
</style>



